import { UPLOADS_DATABASE, DATABASE_NAME, updateUploadsDatabase } from '.';
// 打开数据库并返回指定存储对象中的文件列表
export function openDB(storeName) {
    return new Promise((resolve, reject) => {
        if (UPLOADS_DATABASE) {
            handleExistingDatabase(storeName, resolve, reject);
        }
        else {
            handleNewDatabase(storeName, resolve, reject);
        }
    });
}
// 处理已存在的数据库
function handleExistingDatabase(storeName, resolve, reject) {
    if (UPLOADS_DATABASE.objectStoreNames.contains(storeName)) {
        retrieveData(storeName, resolve, reject);
    }
    else {
        createNewObjectStore(storeName, resolve, reject);
    }
}
// 处理新数据库的创建
function handleNewDatabase(storeName, resolve, reject) {
    try {
        const openRequest = window.indexedDB.open(DATABASE_NAME);
        // 设置数据库事件处理器
        openRequest.onupgradeneeded = () => onUpgradeNeeded(openRequest.result, storeName, resolve, reject);
        openRequest.onsuccess = () => onOpenSuccess(openRequest.result, storeName, resolve, reject);
        openRequest.onerror = () => reject(Error(`打开数据库失败: ${openRequest.error}`));
        openRequest.onblocked = () => reject(Error(`数据库被阻塞: ${openRequest.error}`));
    }
    catch (ex) {
        console.warn(`打开数据库时发生异常: ${ex.message}`, ex);
        reject(ex);
    }
}
// 处理数据库升级事件
function onUpgradeNeeded(db, storeName, resolve, reject) {
    updateUploadsDatabase(db);
    try {
        db.createObjectStore(storeName, {});
        db.onversionchange = (event) => {
            if (event.newVersion === null) {
                db.close();
            }
        };
        resolve([]);
    }
    catch (ex) {
        reject(Error(`升级数据库异常: ${ex.message}`));
    }
}
// 处理数据库成功打开事件
function onOpenSuccess(db, storeName, resolve, reject) {
    updateUploadsDatabase(db);
    if (db.objectStoreNames.contains(storeName)) {
        retrieveData(storeName, resolve, reject);
    }
    else {
        createNewObjectStore(storeName, resolve, reject);
    }
}
// 创建新的对象存储
function createNewObjectStore(storeName, resolve, reject) {
    const newVersion = UPLOADS_DATABASE.version + 1;
    UPLOADS_DATABASE.close();
    const newOpenRequest = window.indexedDB.open(DATABASE_NAME, newVersion);
    newOpenRequest.onupgradeneeded = () => onUpgradeNeeded(newOpenRequest.result, storeName, resolve, reject);
}
// 检索数据
function retrieveData(storeName, resolve, reject) {
    handleFileRetrieve(storeName).then(resolve).catch(reject);
}
// 处理文件检索
function handleFileRetrieve(storeName) {
    return new Promise(function (resolve, reject) {
        let transaction;
        try {
            // 创建只读事务
            transaction = UPLOADS_DATABASE.transaction(storeName, 'readonly');
            try {
                const objectStore = transaction.objectStore(storeName);
                const files = [];
                const cursor = objectStore.openCursor();
                // 遍历游标，收集所有文件
                cursor.onsuccess = () => {
                    const iCursor = cursor.result;
                    if (iCursor) {
                        files.push(iCursor.value);
                        iCursor.continue();
                    }
                    else {
                        resolve(files);
                    }
                };
                cursor.onerror = () => {
                    console.warn(`在handleFileRetrieve()中游标操作失败 - 错误代码: ${cursor.error}`, cursor.error);
                };
            }
            catch (ex) {
                reject(Error(`在handleFileRetrieve()中执行事务或打开游标时发生异常 - ${ex.message}`));
            }
        }
        catch (ex) {
            reject(Error(`在handleFileRetrieve()中创建数据库事务时发生异常 - ${ex.message}`));
        }
    });
}
